import React from 'react';
import PropTypes from 'prop-types';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import FormControl from '@material-ui/core/FormControl';
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import withStyles from '@material-ui/core/styles/withStyles';
import MaterialUIForm, {messageMap } from 'react-material-ui-form';
import { FormHelperText } from '@material-ui/core';

const styles = theme => ({
  main: {
    width: 'auto',
    display: 'block', // Fix IE 11 issue.
    marginLeft: theme.spacing.unit * 3,
    marginRight: theme.spacing.unit * 3,
    [theme.breakpoints.up(400 + theme.spacing.unit * 3 * 2)]: {
      width: 400,
      marginLeft: 'auto',
      marginRight: 'auto',
    },
  },
  paper: {
    marginTop: theme.spacing.unit * 8,
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    padding: `${theme.spacing.unit * 2}px ${theme.spacing.unit * 3}px ${theme.spacing.unit * 3}px`,
  },
  avatar: {
    margin: theme.spacing.unit,
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing.unit,
  },
  submit: {
    marginTop: theme.spacing.unit * 3,
  },
  
});

function Login(props) {
  const { classes } = props;
  const customMessageMap = Object.assign(messageMap, {
    isRequired: 'Pole jest wymagane',
    isError: 'Błędny login lub hasło'
  })
  
  return (
    <main className={classes.main}>
      <CssBaseline />
      <Paper className={classes.paper}>
        <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
          Log in
        </Typography>
        <MaterialUIForm className={classes.form} onSubmit={props.handleLoginSubmit} validation={{
          messageMap: customMessageMap
          }}
          >
          <FormControl margin="normal" required fullWidth >
            <InputLabel htmlFor="email">Email Address or Login</InputLabel>
            <Input id="email" name="email" autoComplete="email" autoFocus value="" />
          </FormControl>
          <FormControl margin="normal" required fullWidth >
            <InputLabel htmlFor="password">Password</InputLabel>
            <Input name="password" type="password" id="password" autoComplete="current-password" value="" />
          </FormControl>
          {
            props.loginError?<FormHelperText margin="dense" error={true}>Błędny login lub hasło</FormHelperText>:null
          }
          <Button
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            className={classes.submit}
          >
            Log in
          </Button>
        </MaterialUIForm>
        <Typography className={classes.submit} component="h6">
            First time?
        </Typography>
        <Button
          onClick={props.handleOnClickRegister}
          fullWidth
          variant="contained"
          color="primary">
          Register
        </Button>
      </Paper>
    </main>
  );




}

Login.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Login);